var mongoose = require('mongoose');
import BaseModel from './base.model';
var BaseSchema = BaseModel.schema;
var async = require('async');
import {Schema} from 'mongoose';

var MasterTimelineTaskSchema = new Schema({
  title : String,
  backgroundColor : String,
  warningColor : String,
  timeEstimate : Object,
  notes : String,
  occurrenceObj : Object
})



export default mongoose.model('MasterTimelineTask', MasterTimelineTaskSchema);
